import { propsToAttributes } from "../../../../Blocks/api/block";
import { defaultProps } from "../../../api/defaultProps";
import { Table as TableExt } from "@tiptap/extension-table";

export const tablePropSchema = {
  ...defaultProps,
  cols: { default: 3 },
  rows: { default: 3 },
  withHeaderRow: { default: true },
  handleWidth: { default: 5 },
  cellMinWidth: { default: 25 },
  resizable: { default: false },
  lastColumnResizable: { default: true },
  allowTableNodeSelection: { default: false },
};

const CustomTable = TableExt.extend({
  addAttributes() {
    return propsToAttributes({
      type: "table",
      propSchema: tablePropSchema,
      containsInlineContent: false,
    });
  },
});

export const Table = {
  node: CustomTable.configure({
    resizable: true,
    cellMinWidth: 100,
  }),
  propSchema: tablePropSchema,
};
