import { Editor } from "@tiptap/core";
import { getBlockInfoFromPos } from "../../../helpers/getBlockInfoFromPos";

export const handleEnter = (editor: Editor) => {
  const { node, contentType } = getBlockInfoFromPos(
    editor.state.doc,
    editor.state.selection.from
  )!;
  const selectionEmpty =
    editor.state.selection.anchor === editor.state.selection.head;

  if (contentType.name !== "table" || !selectionEmpty) {
    return false;
  }

  return editor.commands.first(({ state, chain, commands }) => [
    () =>
      commands.command(() => {
        if (node.textContent.length === 0) {
          return commands.BNUpdateBlock(state.selection.from, {
            type: "paragraph",
            props: {},
          });
        }

        return false;
      }),

    () =>
      commands.command(() => {
        if (node.textContent.length > 0) {
          chain()
            .deleteSelection()
            .insertContent({ type: "paragraph", content: [] })
            .run();

          return true;
        }

        return false;
      }),
  ]);
};

export const handleBackspace = (editor: Editor) =>
  editor.commands.first(({ commands }) => [
    // Deletes the selection if it's not empty.
    () => commands.deleteSelection(),
    // Undoes an input rule if one was triggered in the last editor state change.
    () => commands.undoInputRule(),
    // Reverts block content type to a paragraph if the selection is at the start of the block.
    () =>
      commands.command(({ state }) => {
        const { contentType } = getBlockInfoFromPos(
          state.doc,
          state.selection.from
        )!;

        const selectionAtBlockStart =
          state.selection.$anchor.parentOffset === 0;
        const isParagraph = contentType.name === "paragraph";

        if (selectionAtBlockStart && !isParagraph) {
          return commands.BNUpdateBlock(state.selection.from, {
            type: "paragraph",
            props: {},
          });
        }

        return false;
      }),
    // Removes a level of nesting if the block is indented if the selection is at the start of the block.
    () =>
      commands.command(({ state }) => {
        const selectionAtBlockStart =
          state.selection.$anchor.parentOffset === 0;

        if (selectionAtBlockStart) {
          return commands.liftListItem("blockContainer");
        }

        return false;
      }),
    // Merges block with the previous one if it isn't indented, isn't the first block in the doc, and the selection
    // is at the start of the block.
    () =>
      commands.command(({ state }) => {
        const { depth, startPos } = getBlockInfoFromPos(
          state.doc,
          state.selection.from
        )!;

        const selectionAtBlockStart =
          state.selection.$anchor.parentOffset === 0;
        const selectionEmpty = state.selection.anchor === state.selection.head;
        const blockAtDocStart = startPos === 2;

        const posBetweenBlocks = startPos - 1;

        if (
          !blockAtDocStart &&
          selectionAtBlockStart &&
          selectionEmpty &&
          depth > 1
        ) {
          return commands.BNMergeBlocks(posBetweenBlocks);
        }

        return false;
      }),
  ]);
