export type TableToolbarCallbacks = BaseUiElementCallbacks;

export type tableToolbarState = {
  show: boolean;
  referencePosCell: DOMRect;
  referencePosTable?: DOMRect;

  block?: Block;
  colIndex?: number;
  rowIndex?: number;

  draggingState?:
    | {
        draggedCellOrientation: "row" | "col";
        originalIndex: number;
        mousePos: number;
      }
    | undefined;
};

