import { Extension } from "@tiptap/core";
import { Plugin } from "prosemirror-state";
import { BlockNoteEditor } from "../../BlockNoteEditor";
import { BlockSchema } from "../Blocks/api/blockTypes";

const acceptedMIMETypes = [
  "blocknote/html",
  "text/html",
  "text/plain",
] as const;

export const createPasteFromClipboardExtension = <BSchema extends BlockSchema>(
  editor: BlockNoteEditor<BSchema>
) =>
  Extension.create<{ editor: BlockNoteEditor<BSchema> }, undefined>({
    name: "pasteFromClipboard",
    addProseMirrorPlugins() {
      return [
        new Plugin({
          props: {
            handleDOMEvents: {
              paste(_view, event) {
                event.preventDefault();
                let format: (typeof acceptedMIMETypes)[number] | null = null;

                for (const mimeType of acceptedMIMETypes) {
                  if (event.clipboardData!.types.includes(mimeType)) {
                    format = mimeType;
                    break;
                  }
                }

                if (format !== null) {
                  const data = event.clipboardData!.getData(format);
                  if (format === "text/html") {
                    // const htmlNode = nestedListsToBlockNoteStructure(
                    //   data.trim()
                    // );

                    // data = htmlNode.innerHTML;
                    console.log("paste", data);
                  }
                  editor._tiptapEditor.view.pasteHTML(data);
                }

                return true;
              },
            },
          },
        }),
      ];
    },
  });
