import { defaultProps } from "../../../api/defaultProps";
import { TableCell as TableExt } from "@tiptap/extension-table-cell";
import { handleBackspace, handleEnter } from "./TableCellKeyboardShortcuts";

export const tableCellPropSchema = {
  ...defaultProps,
  colwidth: { default: null },
};

const CustomTableCell = TableExt.extend({
  content: "blockContainer+",
  addAttributes() {
    return {
      ...this.parent?.(),
      backgroundColor: {
        default: "default",
        parseHTML: (element) => element.getAttribute("data-background-color"),
        renderHTML: (attributes) => {
          return {
            "data-background-color": attributes.backgroundColor,
            style: `background-color: ${attributes.backgroundColor}`,
          };
        },
      },
      textColor: {
        default: "default",
        parseHTML: (element) => element.getAttribute("data-text-color"),
        renderHTML: (attributes) => {
          return {
            "data-text-color": attributes.textColor,
            style: `color: ${attributes.textColor}`,
          };
        },
      },
      colwidth: {
        default: null,
        parseHTML: (element) => {
          const colwidth = element.getAttribute("colwidth");
          const value = colwidth ? [parseInt(colwidth, 10)] : null;
          return value;
        },
        renderHTML: (attributes) => {
          return {
            "data-colwidth": attributes.colwidth,
          };
        },
      },
    };
  },
  addKeyboardShortcuts() {
    return {
      Enter: () => handleEnter(this.editor),
      Backspace: () => handleBackspace(this.editor),
    };
  },
});

export const TableCell = {
  node: CustomTableCell,
  propSchema: tableCellPropSchema,
};
